html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #FFFFFF;
  font-family: "Quicksand", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  font-family: Arial, Helvetica, sans-serif;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  font-family: "Roboto Condensed", sans-serif;
  color: #000;
}

.btn {
  background-color: #FFFFFF;
  padding: 2rem 2rem;
  color: #000;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #4658ff;
  color: #FFFFFF;
  transition: 0.8s;
}

.boton-negro {
  background-color: #000;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: #4658ff;
  color: #FFFFFF;
  transition: 0.8s;
}

.header {
  margin: 0.8rem 0 0 2rem;
  padding: 0.2rem;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  margin: 1rem;
  align-items: center;
}

.navegacion__enlace:hover {
  text-decoration: underline;
  color: #4658ff;
  transition: 0.5s;
}

.navegacion {
  padding: 1rem;
  margin: 1rem 0 1rem 1rem;
  justify-content: flex-end;
}
.navegacion__enlace {
  text-transform: none;
  flex-direction: row;
  margin-left: 2rem;
}

.social {
  align-items: row;
  display: flex;
  justify-content: flex-end;
}
.social__icono {
  text-transform: uppercase;
  flex-direction: column;
  margin-right: 1rem;
}

.social__icono:hover {
  text-decoration: underline;
  color: #4658ff;
  transition: 0.5s;
}

.slider {
  overflow: hidden;
  position: relative;
}
.slider__slide {
  height: 100%;
  flex-shrink: 0;
  width: 100%;
  position: relative;
  transition-property: transform;
}
.slider__imagen {
  width: 100%;
}
.slider .swiper-button-next,
.slider .swiper-button-prev {
  color: #FFFFFF;
}
.slider .swiper-pagination-bullet {
  background-color: #FFFFFF;
  opacity: 0.5;
}
.slider .swiper-pagination-bullet-active {
  background-color: #4658ff;
}

.content {
  margin: 5rem;
  padding: 3.5rem;
  background-color: #4658ff;
  color: #FFFFFF;
}

.tiendas {
  display: flex;
  flex-direction: row;
  text-transform: uppercase;
  justify-content: center;
}

.footer {
  margin: 2rem;
  padding: 0.2rem;
}
.footer__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__grid {
    flex-direction: row;
    justify-content: space-around;
    padding: 1rem;
    margin: 2rem;
  }
}
@media (min-width: 1024px) {
  .footer__grid {
    flex-direction: row;
    justify-content: space-around;
    padding: 1rem;
    margin: 2rem;
  }
}
.footer__logo {
  margin: 1rem;
  align-items: center;
}

.nav {
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
  color: #FFFFFF;
  text-align: end;
}

.nav2 {
  text-transform: none;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.nav2__enlace:hover {
  text-decoration: underline;
  color: #4658ff;
  transition: 0.5s;
}

.tfooter {
  text-align: center;
  opacity: 0.3;
}

.nosotros {
  margin: 0.5rem;
  padding: 1rem;
  line-height: 1.6;
  justify-content: space-around;
}
@media (min-width: 768px) {
  .nosotros {
    margin: 2rem;
    padding: 1rem;
  }
}
@media (min-width: 420px) {
  .nosotros {
    margin: 1rem 2rem 1rem 2rem;
    border: 1rem;
    padding: 7rem;
  }
}

@media (min-width: 420px) {
  .section {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    margin: 1rem;
    padding: 1rem;
    place-items: center;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .section {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
    place-items: center;
  }
  .section__grid {
    text-transform: none;
    text-align: center;
  }
}
@media (min-width: 1024px) {
  .section {
    margin: 7rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    place-items: center;
  }
  .section__grid {
    text-transform: none;
    text-align: center;
  }
}

.imagen {
  display: grid;
  place-items: center;
  opacity: 25%;
  padding: 0.5rem;
  margin: 1rem;
}

.imagen:hover {
  opacity: 100%;
  transition: 0.5s;
}

.formulario {
  max-width: 60rem;
  margin: 0 auto;
}

.formulario fieldset {
  border: 1px solid #4658ff;
  margin-bottom: 2rem;
}

.formulario legend {
  background-color: #4658ff;
  width: 100%;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  font-weight: 900;
  padding: 1rem;
  margin-bottom: 4rem;
  font-family: "Roboto Condensed", sans-serif;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
}

.campo label {
  flex-basis: 10rem;
}

.campo input:not([type=radio]),
textarea,
.campo select {
  flex: 1;
  border: 1px solid #e1e1e1;
  padding: 1rem;
}

.btn {
  text-transform: uppercase;
  border: 1px solid #4658ff;
}

.conten {
  display: grid;
  place-items: center;
}