@use 'base/variables' as v;

h1, h2, h3 {
    margin: 0;
    line-height: 1.5; 
}
h1 {
    
    font-size: 4rem;
}
h2 {
    font-size: 3.2rem;
}
h3 {
    font-size: 2.4rem;
}
[class$="heading"] {
    margin: 2rem 0;
}
a {
    text-decoration: none; 
    font-family: v.$fuenteLinks;
    color: v.$negro;

} 
 